//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/reference/attributes.html
//  - [English] http://www.cocos2d-x.org/docs/creator/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/en/scripting/life-cycle-callbacks.html

const GAME_STATE = cc.Enum({
	PREPARE: -1,
	PLAY: -1,
	DEAD: -1,
	WIN: -1
});
const TOUCH_STATE = cc.Enum({
	BLANK: -1,
	FLAG: -1,
});

cc.Class({
	extends: cc.Component,

	properties: {
		tilesLayout: cc.Node,
		tile: cc.Prefab,
		btnShow: cc.Node,
		tiles: [],//��һ�����鱣������tile�����ã������±������Ӧtile��tag
		picPrepare: cc.SpriteFrame,
		picPlay: cc.SpriteFrame,
		picDead: cc.SpriteFrame,
		picWin: cc.SpriteFrame,
		gameState: {
			default: GAME_STATE.PREPARE,
			type: GAME_STATE,
		},
		touchState: {//����㿪tile���Ҽ�����
			default: TOUCH_STATE.BLANK,
			type: TOUCH_STATE,
		},
		row: 0,
		col: 0,
		bombNum: 0,

		Score: cc.Node,
		Time: cc.Node,
	},

	onLoad: function () {
		this.Tile = require("Tile");
		this.roundClear = false;
		this.alreadyStarted = false;
		var self = this;
		for (let y = 0; y < this.row; y++) {
			for (let x = 0; x < this.col; x++) {
				let tile = cc.instantiate(this.tile);
				tile.tag = y * this.col + x;
				tile.on(cc.Node.EventType.MOUSE_DOWN, function (event) {
					if (event.getButton() === cc.Event.EventMouse.BUTTON_LEFT &&
						cc.Event.EventMouse.BUTTON_RIGHT) {
						self.roundClear = self.openNum(this);
					}
				});
				tile.on(cc.Node.EventType.MOUSE_UP, function (event) {
					if (event.getButton() === cc.Event.EventMouse.BUTTON_LEFT) {
						self.touchState = TOUCH_STATE.BLANK;
					} else if (event.getButton() === cc.Event.EventMouse.BUTTON_RIGHT) {
						self.touchState = TOUCH_STATE.FLAG;
					}
					self.stopWatch(GAME_STATE.PLAY);
					self.onTouchTile(this);
				});
				this.tilesLayout.addChild(tile);
				this.tiles.push(tile);
			}
		}
		this.newGame();

		this.btnShow.on(cc.Node.EventType.MOUSE_UP, function (event) {
			self.onBtnShow();
		});
	},

	newGame: function () {
		//��ʼ������
		for (let n = 0; n < this.tiles.length; n++) {
			this.tiles[n].getComponent("Tile").type = this.Tile.TYPE.ZERO;
			this.tiles[n].getComponent("Tile").state = this.Tile.STATE.NONE;
		}
		this.restBmob = this.bombNum;
		this.Score.getComponent(cc.Label).string = this.restBmob;
		this.time = 0;
		this.Time.getComponent(cc.Label).string = this.time;

		//�����
		var tilesIndex = [];
		for (var i = 0; i < this.tiles.length; i++) {
			tilesIndex[i] = i;
		}
		for (var j = 0; j < this.bombNum; j++) {
			var n = Math.floor(Math.random() * tilesIndex.length);
			this.tiles[tilesIndex[n]].getComponent("Tile").type = this.Tile.TYPE.BOMB;
			tilesIndex.splice(n, 1);//�ӵ�n��λ��ɾ��һ��Ԫ��
			//���û��splice�������������ַ�ʽ
			// tilesIndex[n] = tilesIndex[tilesIndex.length-1];
			// tilesIndex.length--;
		}
		//�������Χ�ķ���
		for (var k = 0; k < this.tiles.length; k++) {
			var tempBomb = 0;
			if (this.tiles[k].getComponent("Tile").type == this.Tile.TYPE.ZERO) {
				var roundTiles = this.tileRound(k);
				for (var m = 0; m < roundTiles.length; m++) {
					if (roundTiles[m].getComponent("Tile").type == this.Tile.TYPE.BOMB) {
						tempBomb++;
					}
				}
				this.tiles[k].getComponent("Tile").type = tempBomb;
			}
		}

		this.gameState = GAME_STATE.PLAY;
		this.btnShow.getComponent(cc.Sprite).spriteFrame = this.picPlay;
	},

	//����tagΪi��tile����Χtile����
	tileRound: function (i) {
		var roundTiles = [];
		if (i % this.col > 0) {//left
			roundTiles.push(this.tiles[i - 1]);
		}
		if (i % this.col > 0 && Math.floor(i / this.col) > 0) {//left bottom
			roundTiles.push(this.tiles[i - this.col - 1]);
		}
		if (i % this.col > 0 && Math.floor(i / this.col) < this.row - 1) {//left top
			roundTiles.push(this.tiles[i + this.col - 1]);
		}
		if (Math.floor(i / this.col) > 0) {//bottom
			roundTiles.push(this.tiles[i - this.col]);
		}
		if (Math.floor(i / this.col) < this.row - 1) {//top
			roundTiles.push(this.tiles[i + this.col]);
		}
		if (i % this.col < this.col - 1) {//right
			roundTiles.push(this.tiles[i + 1]);
		}
		if (i % this.col < this.col - 1 && Math.floor(i / this.col) > 0) {//rihgt bottom
			roundTiles.push(this.tiles[i - this.col + 1]);
		}
		if (i % this.col < this.col - 1 && Math.floor(i / this.col) < this.row - 1) {//right top
			roundTiles.push(this.tiles[i + this.col + 1]);
		}
		return roundTiles;
	},

	onTouchTile: function (touchTile) {
		if (this.gameState != GAME_STATE.PLAY) {
			return;
		}
		switch (this.touchState) {
			case TOUCH_STATE.BLANK:
				if (touchTile.getComponent("Tile").type === 9) {
					touchTile.getComponent("Tile").state = this.Tile.STATE.CLIKED;
					this.gameOver();
					return;
				}


				//����ѵ㿪�ĵ�λչʾ��ΧһȦ��λ
				if (touchTile.getComponent("Tile").state === this.Tile.STATE.CLIKED) {
					console.log(this.roundClear);
					if (this.roundClear === false) {
						let roundTiles = this.tileRound(touchTile.tag);
						for (let i = 0; i < roundTiles.length; i++) {
							if (roundTiles[i].getComponent("Tile").state === this.Tile.STATE.PRESS) {
								roundTiles[i].getComponent("Tile").state = this.Tile.STATE.NONE;
							}
						}
					}

					if (this.roundClear === true) {
						console.log("touchTile.Cliked");//-------
						var selfType = touchTile.getComponent("Tile").type;
						let roundTiles = this.tileRound(touchTile.tag);
						console.log("selfType" + selfType); //---------------

						let roundNone = [];
						for (let i = 0; i < roundTiles.length; i++) {
							if (roundTiles[i].getComponent("Tile").state === this.Tile.STATE.FLAG) {
								selfType--;
							}
							else {
								roundNone.push(roundTiles[i]);
							}
						}
						let roundBlank = [];
						console.log("selfType" + selfType);//-----------------
						if (selfType == 0) {
							console.log("rest = 0");//-------------
							var touchBmob = false;
							while (roundNone.length) {
								console.log("enter loop");//----------
								let tileNone = roundNone.pop();
								tileNone.getComponent("Tile").state = this.Tile.STATE.CLIKED;
								if (tileNone.getComponent("Tile").type === this.Tile.TYPE.BOMB) {
									touchBmob = true;
								}
								if (tileNone.getComponent("Tile").type === this.Tile.TYPE.ZERO) {
									roundBlank = this.tileRound(tileNone.tag);
									for (let i = 0; i < roundBlank.length; i++) {
										if (roundBlank[i].getComponent("Tile").state === this.Tile.STATE.NONE &&
											roundBlank[i].getComponent("Tile").type === this.Tile.TYPE.ZERO) {
											roundNone.push(roundBlank[i]);
											roundBlank[i].getComponent("Tile").state = this.Tile.STATE.CLIKED;
										}
										else { roundBlank[i].getComponent("Tile").state = this.Tile.STATE.CLIKED; }
									}
								}
							}
							if (touchBmob) {
								this.gameOver();
							}
						}
						if (selfType != 0) {
							let roundBlank = [];

						}
					}
				}

				var testTiles = [];
				if (touchTile.getComponent("Tile").state === this.Tile.STATE.NONE) {
					console.log("touchTile.None");
					testTiles.push(touchTile);
					while (testTiles.length) {
						var testTile = testTiles.pop();
						if (testTile.getComponent("Tile").type === 0) {
							testTile.getComponent("Tile").state = this.Tile.STATE.CLIKED;
							let roundTiles = this.tileRound(testTile.tag);
							for (var i = 0; i < roundTiles.length; i++) {
								if (roundTiles[i].getComponent("Tile").state == this.Tile.STATE.NONE) {
									testTiles.push(roundTiles[i]);
								}
							}
						} else if (testTile.getComponent("Tile").type > 0 && testTile.getComponent("Tile").type < 9) {
							testTile.getComponent("Tile").state = this.Tile.STATE.CLIKED;
						}
					}
					this.judgeWin();
				}

				break;
			case TOUCH_STATE.FLAG:
				if (touchTile.getComponent("Tile").state == this.Tile.STATE.NONE) {
					touchTile.getComponent("Tile").state = this.Tile.STATE.FLAG;
					this.restBmob--;
					this.Score.getComponent(cc.Label).string = this.restBmob;
				} else if (touchTile.getComponent("Tile").state == this.Tile.STATE.FLAG) {
					touchTile.getComponent("Tile").state = this.Tile.STATE.NONE;
				}
				break;
			default: break;
		}

	},

	openNum: function (tile) {
		if (this.gameState != GAME_STATE.PLAY) {
			return;
		}
		if (tile.getComponent("Tile").state === this.Tile.STATE.CLIKED) {
			let type = tile.getComponent("Tile").type;
			let round = [];
			round = this.tileRound(tile.tag);
			let roundPress = [];
			for (let i = 0; i < round.length; i++) {
				if (round[i].getComponent("Tile").state === this.Tile.STATE.NONE) {
					round[i].getComponent("Tile").state = this.Tile.STATE.PRESS;
				}
				else if (round[i].getComponent("Tile").state === this.Tile.STATE.FLAG) {
					type--;
				}
			}
			console.log(type);
			if (type === 0) {
				return true;
			}
			else if (type !== 0) {
				return false;
			}
		}
	},

	judgeWin: function () {
		var confNum = 0;
		//�ж��Ƿ�ʤ��
		for (let i = 0; i < this.tiles.length; i++) {
			if (this.tiles[i].getComponent("Tile").state === this.Tile.STATE.CLIKED) {
				confNum++;
			}
		}
		if (confNum === this.tiles.length - this.bombNum) {
			this.gameState = GAME_STATE.WIN;
			this.btnShow.getComponent(cc.Sprite).spriteFrame = this.picWin;
		}
	},

	gameOver: function () {
		this.gameState = GAME_STATE.DEAD;
		this.btnShow.getComponent(cc.Sprite).spriteFrame = this.picDead;
		this.stopWatch(this.gameState);
	},




	onBtnShow: function () {
		if (this.gameState === GAME_STATE.PREPARE) {
			this.newGame();
		}
		if (this.gameState === GAME_STATE.DEAD) {
			// this.bombNum--;
			this.newGame();
		}
		if (this.gameState === GAME_STATE.WIN) {
			// this.bombNum++;
			this.newGame();
		}
	},

	stopWatch: function (gameState) {
		
		this.timeup = function () {
			this.Time.getComponent(cc.Label).string++;
		};
		var self = this;
		if (gameState ===GAME_STATE.PLAY && this.alreadyStarted === false) {
			this.alreadyStarted = true;
			this.Time.getComponent(cc.Label).schedule(function () {
				self.timeup();
			}, 1);
		}
		if (gameState === GAME_STATE.DEAD) {
			this.alreadyStarted = false;
			console.log("stop");
			this.Time.getComponent(cc.Label).unscheduleAllCallbacks();
		}
	}

});